form {
    display: flex;
}

form input {
    background-color: transparent;
    border: 2px solid #148CA4;
    font-size: 16px;
    color: #EBEBEB;
    outline: none;
    padding: 12px 12px;
    width: 500px;
    border-radius: 4px 0 0 4px;
    font-weight: 400;
    letter-spacing: .7px;
    text-shadow: none;
}

::placeholder {
    text-shadow: none;
    user-select: none;
}

form #add-btn {
    font-size: 14px;
    height: 100%;
    width: 120px;
    border-radius: 0 4px 4px 0;
    text-shadow: none;
    cursor: pointer;
    background-color: #148CA4;
    user-select: none;
    text-shadow: 0px 3px 4px #111111;

    transition: all .15s;
}

form #add-btn:hover {
    text-shadow: none;
    background-color: #19a6c2;
}


@media (max-width: 850px) {
    form input {
        padding: 12px 12px;
        width: 300px;
    }
    
    form #add-btn {
        width: 80px;
    }
}