.Header {
    width: 100%;
    height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #232323;
}

.Header h1 {
    margin-bottom: 20px;
    font-weight: 800;
    letter-spacing: 1.5px;
    font-size: 40px;
    user-select: none;
}

.Header h1 span {
    color: #148CA4;
}

@media (max-width: 850px) {
	.Navbar {
		height: 80px;
	}

	.Header h1 {
		margin-bottom: 10px;
		font-size: 35px;
	}
}