.Task {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 220px;
    background-color: #232323;
    border-radius: 6px;
    margin-bottom: 16px;
    transition: all .18s linear;
    overflow: hidden;
}

.task-description-container {
    padding: 28px 12px 28px 40px;
    display: grid;
    grid-template-columns: 20px 400px;
}

.task-text-container {
    margin-left: 20px;
    overflow: auto;
}

div.task-completed small {
    margin: 20px;
    font-style: italic;
} 

.task-text-container::-webkit-scrollbar {
    display: none;
}

.task-description-container .task-text-container {
    font-size: 18px;
    display: flex;
    align-items: center;

}

.task-btn-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.task-btn-con.done {
    grid-template-columns: 1fr;
}

.task-btn-con.done .delete-btn {
    border-radius: 6px;
}

.task-btn {
    font-size: 16px;
    text-shadow: 0px 3px 4px #111111;
    background-color: transparent;
    cursor: pointer;
    user-select: none;

    transition: all .3s;
}

.edit-btn {
    border-radius: 6px 0 0 6px;
}

.delete-btn {
    border-radius: 0 6px 6px 0;
}


.edit-btn:hover {
    background-color: #148ca4df;
}

.delete-btn:hover {
    background-color: #a92314df;
}

.delete-btn:active,
.edit-btn:active {
    text-shadow: none;
}

.edit-btn:active {
    background-color: #13adcbdf;
}

.delete-btn:active {
    background-color: #cd2613df;
}

div.Task.done {
    opacity: 40%;
    box-shadow: none;
}

.task-text-container .task-name {
    cursor: pointer;
    letter-spacing: .5px;
    position: relative;
    color: #148ca4;
    font-weight: 700;
    text-transform: capitalize;
    font-size: 20px;
}

.task-text-container .task-name::before {
    content: '';
    display: block;
    width: 0;
    height: 1.5px;
    background-color: #ebebeb;
    top: 50%;
    position: absolute;
    transition: all .18s linear;
    text-shadow: 0px 3px 4px #111111;
}

.Task.done .task-text-container .task-name::before {
    width: 100%;
}

.task-description-container input {
    width: 410px;
    border-radius: 4px;
}

@media (max-width: 850px) {
	.Task {
        display: grid;
        grid-template-columns: 1fr 120px;
    }

    .task-description-container {
        padding: 10px 0 10px 20px;
        grid-template-columns: 20px 150px;
    }
    
    div.task-completed small {
        font-size: 8px;
        margin-left: 10px;
        font-style: italic;
    }
    .task-text-container {
        margin-left: 10px;
    }

    .task-completed {
        margin: 0;
        padding: 0;
        width: 100%;
    }
    
    .task-btn {
        font-size: 12px;
    }

    .task-description-container input {
        width: 300px;
        font-size: 14px;
    }

    .task-text-container .task-name {
        font-size: 16px;
    }

}






