.Navbar {
    font-style: none;
    width: 700px;
    margin: auto;
    padding-top: 32px;
}

.Navbar ul {
    display: flex;
    justify-content: start;
    margin:  0 0 32px 0;
}

.Navbar ul li {
    margin-right: 22px;
    font-weight: 600;
    letter-spacing: .5px;
    transition: all .18s linear;
    user-select: none;
    cursor: pointer;
    position: relative;
}

.Navbar ul li:hover {
    color: #148CA4;
}

.Navbar ul li.active {
    color: #148CA4;
} 

.Navbar ul li:before {
    content: '';
    height: 2px;
    border-radius: 6px;
    width: 0;
    top: 24px;
    position: absolute;
    transition: all .18s linear;
    background-color: #148CA4;
    text-shadow: 0px 3px 4px #111111;
}

.Navbar ul li.active::before {
    width: 100%;
}



@media (max-width: 850px) {
    .Navbar {
        width: 400px;
        margin: auto;
        padding-top: 18px;
    }

    .Navbar ul {
        justify-content: center;
    }
}