.Checkbox, 
.unchecked,
.checked {
    display: flex;
    align-items: center;
}

svg.unchecked-box {
    height: 24px;
    width: 24px;
    transition: all .15s;
    fill: #EBEBEB;
    cursor: pointer;
    user-select: none;
}

svg.unchecked-box:hover {
    fill: #148CA4;
}

svg.checked-box {
    height: 24px;
    width: 24px;
    transition: all .15s;
    fill: #148CA4;
    cursor: pointer;
    user-select: none;
}
